	import { mapGetters } from 'vuex';
export default {
	data() {
		return {
			countDown: '00:15:00',
			timer: null,
			timeOut: false
		};
	},
	onLoad() {
		this.countTimeDown()
	},
	computed: {
		...mapGetters(['orderInfo', 'totalPrice', 'remarkInfo', 'userInfo'])
	},
	methods: {
		countTimeDown() {
			let minute = 14;
			let second = 59;

			this.timer = setInterval(() => {
				second--;

				if (second == '00' && minute == '00') {
					this.countDown = '订单已超时';
					clearInterval(this.timer);
					this.timeOut = true;
					return;
				}

				if (second == '00') {
					second = 59;
					minute--;

					if (minute < 10) {
						minute = '0' + minute;
					}
				}

				if (second < 10) {
					second = '0' + second;
				}

				this.countDown = '00:' + minute + ':' + second;
			}, 1000);
		},
		pay() {
			uni.showToast({
				title: '跳到支付页面'
			})
		}
	},
	components: {}
};
